<template>
    <div class = "eventEditDialog">
        <v-dialog v-model="show" max-width="70%" :fullscreen ="$vuetify.breakpoint.mobile" overlay-opacity=".8" persistent >
            <v-card outlined >
                <!-- <v-card-title><span class="headline">{{ formTitle }}</span></v-card-title> -->
                <v-card-title class="headline">{{dialogTitle}}<v-spacer></v-spacer><v-btn icon @click="show=false"><v-icon>{{$icon.mdiClose}}</v-icon></v-btn></v-card-title>

                <v-stepper v-model="curr"  >
                <v-stepper-header >
                    <template v-for="(step,n) in steps">
                        <v-stepper-step :editable="mode ==='edit'" :key="`${n}-step`" :complete="stepComplete(n+1)" :step="n+1" :rules="[value => !!step.valid]" :color="stepStatus(n+1)">{{ step.name }} </v-stepper-step>
                        <v-divider v-if="n !== steps.length-1" :key="n"></v-divider>
                    </template>
                </v-stepper-header>
                <v-stepper-content step="1" >
                    
                    <v-card  class="mb-12" >
                        <v-form ref="1" lazy-validation>
                        <v-card-title >{{steps[curr-1].name}}<v-spacer></v-spacer><v-btn icon color="primary"><v-icon>{{$icon.mdiInformationOutline}}</v-icon></v-btn></v-card-title>
                        <v-card-subtitle>Select a Event Type and what Group this is for.</v-card-subtitle>
                        <v-card-text>
                            <v-container >
                                    <v-row>
                                        <v-col cols="12" sm="12" md="4" lg="3"><v-select outlined v-model="editedItems.type" label="Event Type" :items="defaultItem.type" :rules ="$rules.required" :disabled="editMode || allDisabled" hint="Required" persistent-hint></v-select></v-col>
                                        <v-col cols="12" sm="12" md="4" lg="3"><v-combobox outlined v-model="editedItems.group" label="Group" :items="groupAll" :rules ="$rules.required.concat($rules.noSpaces)" :disabled="editMode || allDisabled"  hint="Required - No Spaces" persistent-hint></v-combobox></v-col>
                                    </v-row>
                            </v-container>
                        </v-card-text>
                        </v-form>
                    </v-card>
                    <div class="d-flex" :class="{'justify-space-around':$vuetify.breakpoint.mobile}">
                    <v-btn text disabled >Back</v-btn> 
                    <v-btn color="primary" @click="validate(1)" >Continue</v-btn>
                    <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                    <!-- <v-btn v-if="mode == 'edit'" color="accent" @click="getEmailPreviewTags()">Preview</v-btn>  -->
                     </div>
                     <div class="d-flex mt-6" v-if="mode == 'edit'">
                            <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                            <v-btn :width="mobileWidth" color="accent"  @click="getEmailPreviewTags()">Preview</v-btn> 
                        </div>
                </v-stepper-content>
                <v-stepper-content step="2" >
                    
                    <v-card  class="mb-12"> 
                        <v-form ref="2" lazy-validation>
                        <v-card-title>{{steps[curr-1].name}}<v-spacer></v-spacer><v-btn icon color="primary"><v-icon>{{$icon.mdiInformationOutline}}</v-icon></v-btn></v-card-title>
                        <v-card-subtitle>Select a Readings ID and assign it a name.</v-card-subtitle>
                        <v-card-text >
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="12" md="4" lg="2"><v-select outlined v-model="editedItems.register_id" label="ID" :items="$readingsIDs.sort()" @change="addAlias" :rules ="$rules.required" :disabled="allDisabled" hint="Required" persistent-hint></v-select></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="3"><v-text-field outlined v-model="editedItems.alias" label="Alias" :disabled="allDisabled"></v-text-field></v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        </v-form>
                    </v-card>
                    <div class="d-flex" :class="{'justify-space-around':$vuetify.breakpoint.mobile}">
                    <v-btn text @click="curr = 1">Back</v-btn> 
                    <v-btn color="primary" @click="validate(2)" >Continue</v-btn>
                    <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                    <!-- <v-btn v-if="mode == 'edit'" color="accent" @click="getEmailPreviewTags()">Preview</v-btn>  -->
                     </div>
                     <div class="d-flex mt-6" v-if="mode == 'edit'">
                            <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                            <v-btn :width="mobileWidth" color="accent"  @click="getEmailPreviewTags()">Preview</v-btn> 
                        </div>
                </v-stepper-content>
                <v-stepper-content step="3">
                    
                    <v-card  class="mb-12" >
                        <v-form ref="3" lazy-validation>
                        <v-card-title>{{steps[curr-1].name}}<v-spacer></v-spacer><v-btn icon color="primary"><v-icon>{{$icon.mdiInformationOutline}}</v-icon></v-btn></v-card-title>
                        <v-card-subtitle>Enter the Events Parameters.</v-card-subtitle>
                        <v-card-text >
                            <v-container >
                                <v-row>
                                    <v-col cols="12" sm="12" md="4" lg="3"><v-select outlined label="Readings Value" v-model="editedItems.event.readingsValue" :items="defaultItem.event.readingsValue" :rules ="$rules.required" :disabled="allDisabled" hint="Required" persistent-hint></v-select></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="3"><v-select outlined v-model="editedItems.event.comparison" label="Comparison Type" :items="defaultItem.event.comparison" :rules ="$rules.required" :disabled="allDisabled" hint="Required" persistent-hint></v-select></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="4"><v-text-field outlined v-model="editedItems.event.value" label="Value" required :rules="$rules.required" :disabled="allDisabled" hint="Required" persistent-hint></v-text-field></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="2"><v-select outlined label="Event Level" v-model="editedItems.event.level" :items="defaultItem.event.level" :rules ="$rules.required" :disabled="allDisabled" hint="Required" persistent-hint></v-select></v-col>
                                </v-row>
                                <v-row><v-col cols="12" sm="12" md="4" lg="4"><v-combobox outlined small-chips multiple label="Event Tags" v-model="editedItems.event.eventTag" :disabled="allDisabled" v-show="notEmail" :items="tagAll" ></v-combobox></v-col></v-row>
                            </v-container>
                        </v-card-text>
                        </v-form>
                    </v-card>
                    <div class="d-flex" :class="{'justify-space-around':$vuetify.breakpoint.mobile}" >
                    <v-btn text @click="curr = 2">Back</v-btn> 
                    <v-btn color="primary" @click="validate(3)" >Continue</v-btn>
                    <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                    <!-- <v-btn v-if="mode == 'edit'" color="accent" @click="getEmailPreviewTags()">Preview</v-btn>  -->
                     </div>
                     <div class="d-flex mt-6" v-if="mode == 'edit'">
                            <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                            <v-btn :width="mobileWidth" color="accent"  @click="getEmailPreviewTags()">Preview</v-btn> 
                        </div>
                </v-stepper-content>
                
                <v-stepper-content step="4">
                    
                    <v-card  class="mb-12" >
                        <v-form ref="4" lazy-validation >
                        <v-card-title>{{steps[curr-1].name}}<v-spacer></v-spacer><v-btn icon color="primary"><v-icon>{{$icon.mdiInformationOutline}}</v-icon></v-btn></v-card-title>
                        <v-card-subtitle>Enter any options for this Event.</v-card-subtitle>
                        <v-card-text >
                            
                            <v-container >
                                <v-row>
                                    <v-col cols="12" sm="12" md="4" lg="2"><v-text-field  outlined v-model="editedItems.event.options.buffer" label="Buffer" :rules="$rules.numberOrBeEmpty" :disabled="allDisabled" hint="Minutes" persistent-hint></v-text-field></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="2"><v-select clearable outlined v-model="editedItems.event.options.oneShot" label="One Shot" :disabled="allDisabled || oneShotDisabled" :items="[true]" hint="Cannot be used with Suppress" persistent-hint></v-select></v-col>
                                    <v-col cols="12" sm="12" md="4" lg="2"><v-text-field outlined  v-model="editedItems.event.options.suppress" label="Suppress" :rules="$rules.numberOrBeEmpty" :disabled="allDisabled || suppressDisabled" hint="Hours" persistent-hint></v-text-field></v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        </v-form>
                    </v-card>
                    <div class="d-flex " :class="{'justify-space-around':$vuetify.breakpoint.mobile}">
                        <v-btn text  @click="curr = 3">Back</v-btn> 
                        <!-- <v-btn width="100" :class="{'mr-4':!$vuetify.breakpoint.mobile}" color="primary" @click="validate(4)" >Continue</v-btn> -->
                        <v-btn width="100" color="success" @click="done()" >Done</v-btn>
                        

                        <!-- <v-btn v-if="showPreview" color="accent"  @click="getEmailPreviewTags()">Preview</v-btn> --> 
                        
                     </div>
                     <div class="d-flex mt-6" >
                            <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                            <v-btn :width="mobileWidth" color="accent"  @click="getEmailPreviewTags()">Preview</v-btn> 
                        </div>
                     
                </v-stepper-content>
                <v-stepper-content step="5" :class="{'px-0':$vuetify.breakpoint.mobile}">
                    
                    <v-card  class="mb-12" :width="$vuetify.breakpoint.mobile ? '' :'100%'" >
                        <v-form ref="5" lazy-validation>
                        <v-card-title>{{steps[curr-1].name}}<v-spacer></v-spacer><v-btn icon color="primary"><v-icon>{{$icon.mdiInformationOutline}}</v-icon></v-btn></v-card-title>
                        <v-card-subtitle>A preview of what the email will look like if this event is triggred.</v-card-subtitle>
                        <v-card-text >

                            <v-card :width="$vuetify.breakpoint.mobile ? '' :'50%'" outlined disabled>
                                <v-card-text class="overline">
                                    
                                    <span>SUBJECT: {{$view.facility.name}} NSITE Event</span>
                                </v-card-text>
                                <v-card elevation="16" class="mx-4 mb-4" outlined>
                                <v-card-text class="subtitle-1">
                                    <span >{{editedItems.alias}} is {{editedItems.event.comparison}}<span v-if="editedItems.event.comparison ==='equal'"> to </span><span v-else> - </span>{{editedItems.event.value}}</span><br>
                                    <span class="overline">Event Group: {{editedItems.group}}</span><br>
                                </v-card-text>
                                <v-card-text v-for="(tag, key) in tags" :key="key">
                                    <div class="overline">
                                        <span>{{tag}}</span>
                                    </div>
                                    <v-simple-table dense >
                                        <template v-slot:default>
                                        <thead>
                                            <tr>
                                            <th class="text-left">
                                                Name
                                            </th>
                                            <th class="text-left">
                                                Value
                                            </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr
                                            v-for="(item, key) in getEmailPreviewTable(tag)" :key="key"
                                            
                                            >
                                            <td>{{ item.name }}</td>
                                            <td>{{ item.value }}</td>
                                            </tr>
                                        </tbody>
                                        </template>
                                    </v-simple-table>
                                </v-card-text>
                                </v-card>
                            </v-card>
                        </v-card-text>
                      </v-form>
                    </v-card>
                    <div class="d-flex " :class="{'justify-space-around':$vuetify.breakpoint.mobile}">
                    <v-btn text @click="curr = 4">Back</v-btn> 
                    <v-btn width="100" color="success" @click="done()" >Done</v-btn>
                    <v-spacer v-if="!$vuetify.breakpoint.mobile"></v-spacer>
                    <!-- <v-btn v-if="mode == 'edit'" color="success" @click="curr = 5">Preview</v-btn> --> 
                     </div>
                </v-stepper-content>
            </v-stepper>

            </v-card>
            
        </v-dialog>
    </div>
</template>

<script>
import eventBus from "../eventBus"

export default {
    name: 'eventsEditDialog',
    props:["value",'readings','mode','events','groupName','eventType'],
data: () => ({
        notEmail:false,
        oneShotDisabled: false,
        suppressDisabled:false,
        allDisabled: false,
        groupAll:[],
        tagAll:[],
        dialogTitle:"Events",
        curr: 2,
        lastStep: 5,
        steps: [
            {name: "Type / Group", valid: true},
            {name: "ID / Name",  valid: true},
            {name: "Parameters", valid: true},
            {name: "Options", valid: true},
            {name: "Preview", valid: true},
        ],
        stepForm: [],
        editMode:false,
        editedItems: {},
        defaultItem: {
            register_id: '',
            type:['email','notification','log'],
            alias:'',
            event: {
                comparison:['equal', 'notEqual', 'lessThan', 'lessThanOrEqual', 'graterThan', 'gratherThanOrEqual', 'between', 'notBetween'],
                //comparison:[],
                value:'',
                readingsValue:['value','mathValue','rawValue'],
                eventTag:'',
                level:['error','warning','info'],
                lastEvent: 0,
                eventCount: 0,
                options:{
                    buffer:'',
                    oneShot:'',
                    suppress:'',
                }
            }
        },
        emailPreview:[],
        test:'',
        tags:[]
    }),
    watch:{
        "editedItems.event.options.oneShot": function (){
            if(this.editedItems.event.options.oneShot){
                this.suppressDisabled = true
            } else {
                this.suppressDisabled = false
            }
        },
        "editedItems.event.options.suppress": function (){
            if(this.editedItems.event.options.suppress){
                this.oneShotDisabled = true
            } else {
                this.oneShotDisabled = false
            }
        },
        "editedItems.type": function(){
            if(this.editedItems.type === "email"){
                this.notEmail = true
            }
        }
    },
    created(){
        this.editedItems = this.$clone(this.events)
        if(this.events.register_id.includes('_')) this.$delete(this.defaultItem.event.readingsValue,1)
        this.groupNames()
        this.tagNames()
        switch (this.mode) {
            case "edit":
                if(this.editedItems.group === 'default') this.allDisabled = true
                this.dialogTitle = "Edit Event"
                this.curr = 2
                this.editMode = true
            break;
            case "add":
                //console.log(this.editedItems)
                this.dialogTitle = "Add Event"
                this.curr = 2
                this.editedItems.type = this.eventType
                this.editedItems.group = this.groupName
                this.editMode = true
            break;
            case "create":
                this.dialogTitle = "Create Event"
                this.curr = 1
                this.editMode = false
            break;
            default:
                break;
        }
    },
    methods: {
        numberRule(){
            //console.log("number rule")
        },
        groupNames(){
            /* let vm = this
            this.$http.get('/api/events/groups/',function(err,ret){
                let array = ret.data.results
                if(vm.mode === 'create'){
                    const index = array.indexOf('default')
                    if(index > -1){
                        array.splice(index,1)
                        vm.groupAll = array
                    }
                } else {
                    vm.groupAll = array
                }
            }) */
            this.$http.get('/api/events/groups').then(res => {
                let array = res.results
                if(this.mode === 'create'){
                    const index = array.indexOf('default')
                    if(index > -1){
                        array.splice(index,1)
                        this.groupAll = array
                    }
                } else {
                    this.groupAll = array
                }
            })
        },
        tagNames(){
            /* let vm = this
            this.$http.get("/api/readings/tagNames/",function(err,ret){
            ret.data.results.forEach(x => {
                if(!vm.tagAll.includes(x)) vm.tagAll.push(x)
            })
                vm.tagAll.sort()
            }) */
            this.$http.get('/api/readings/tagNames').then(res => {
                res.results.forEach(x => {
                if(!this.tagAll.includes(x)) this.tagAll.push(x)
            })
                this.tagAll.sort()
            })
  },
        stepComplete(step) {
            return this.curr > step
        },
        stepStatus(step) {
            return this.curr > step ? '#60BB46' : '#005DAC'
        },
        validate(n) {
            
            this.steps[n-1].valid = false
            let v = this.$refs[n].validate()
            //console.log(n)
            if (v) {
                this.steps[n-1].valid = true
                // continue to next
                this.curr = n+1
            } 
        },
        done() {
            //let vm = this
            if(this.editedItems.event.options.buffer === "") delete this.editedItems.event.options.buffer
            if(this.editedItems.event.options.oneShot === "") delete this.editedItems.event.options.oneShot
            if(this.editedItems.event.options.suppress === "") delete this.editedItems.event.options.suppress
            if(this.editedItems.event.eventTag === "") delete this.editedItems.event.eventTag
            this.editedItems.device = this.$root.readingsTest[this.editedItems.register_id].device
            this.editedItems.group = this.editedItems.group.toUpperCase()
            /* this.$http.post('/api/events/addEvent',this.editedItems,function(err,ret){
                console.log(err || ret)
                if(!err){
                    vm.editedItems.uuid = ret.data.ret.results.uuid
                    eventBus.$emit('eventDialog',vm.mode,vm.editedItems)
                    vm.show = false
                }
            }) */
            this.$http.post('/api/events/addEvent',this.editedItems).then(res => {
                this.editedItems.uuid = res.ret.results.uuid
                eventBus.$emit('eventDialog',this.mode,this.editedItems)
                this.show = false
            })
        },
        addAlias(id){
            
           if(Object.prototype.hasOwnProperty.call(this.$tags[id], 'view') ){
                if(Object.prototype.hasOwnProperty.call(this.$tags[id].view, 'alias')){
                    this.editedItems.alias = this.$tags[id].view.alias
                } else {
                    //this.editedItem.event.alias = ''
                }
            } else {
               // this.editedItem.event.alias = ''
            } 
           // console.log(id)
        },
        getEmailPreviewTags(){

            //let vm = this
            if(this.editedItems.event.eventTag){
                this.editedItems.event.eventTag.forEach(tag => {
                    this.tags.push(tag)
                    /* this.$http.get('/api/readings/tag/'+tag,function(err,ret){
                        vm.test = ret.data.resultsArr 
                        vm.test.forEach(x => {
                            Object.keys(x).forEach(y => {
                                const name = x[y].view.alias
                                const value = x[y].value
                                let obj = {}
                                obj.name = name
                                obj.value = value
                                obj.tag = tag
                                vm.emailPreview.push(obj)
                            })
                        });  
                    }) */
                    this.$http.get(`/api/readings/tag/${tag}`).then(res => {
                        this.test = res.resultsArr 
                        this.test.forEach(x => {
                            Object.keys(x).forEach(y => {
                                const name = x[y].view.alias
                                const value = x[y].value
                                let obj = {}
                                obj.name = name
                                obj.value = value
                                obj.tag = tag
                                this.emailPreview.push(obj)
                            })
                        }); 
                    })
                })
            

            } 
            
           // console.log(this.emailPreview)
            this.curr = 5
        },
        getEmailPreviewTable(tag){
            return this.emailPreview.filter(function (x) {
                return x.tag === tag
            })
        }
        
    },
    computed: {
        show: {
            get () {
                return this.value
            },
            set (value) {
                this.$emit('input', value)
            } 
        },
        showPreview(){
            if(this.mode === 'add' || this.mode === 'edit'){
                return true
            } else {
                return false
            }
        },
        mobileWidth(){
            if(this.$vuetify.breakpoint.mobile){
                return '100%'
            } else {
                return ''
            }
        },
  },
}

</script>

<style scoped>

</style>